:root {
  --cards-color: #f0ebe3;
  --body-back: #393e46;
  --teal: #00fff5;
  --darker-teal-btn: #44a6a3;
  --dark-teal: #135d66;
  --light-teal: #e3fef7;
  --darker-teal: #c4dbd5;
  --dark-color: #222831;
}

body {
  background-color: var(--body-back);
  padding: 0;
  font-family: Josefin Sans, sans-serif;
}

html, body {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.list {
  list-style: none;
}

.link {
  color: #000;
  text-decoration: none;
}

.not-visible {
  display: none;
}

.load-more-btn {
  color: var(--dark-teal);
  background-color: var(--teal);
  cursor: pointer;
  width: 150px;
  height: 40px;
  border: none;
  border-radius: 3px;
  margin: 0 auto;
  font-family: Josefin Sans, sans-serif;
  font-size: 15px;
  font-weight: bolder;
  display: block;
}

.top-container {
  height: 70px;
  background-color: var(--dark-color);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  animation: pulsation 4s infinite;
  display: flex;
  position: relative;
}

@keyframes pulsation {
  0% {
    box-shadow: 0 2px 8px rgba(143, 240, 255, .3);
  }

  25% {
    box-shadow: 0 2px 10px rgba(143, 240, 255, .3);
  }

  50% {
    box-shadow: 0 2px 15px rgba(143, 240, 255, .3);
  }

  75% {
    box-shadow: 0 2px 10px rgba(143, 240, 255, .3);
  }

  100% {
    box-shadow: 0 2px 8px rgba(143, 240, 255, .3);
  }
}

.logo-heading {
  display: none;
}

.search-container {
  height: 70px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-inp {
  width: 150px;
  height: 30px;
  background-color: var(--light-teal);
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding-left: 5px;
  font-family: Josefin Sans, sans-serif;
  font-size: 13px;
}

.search-btn {
  width: 70px;
  height: 32px;
  background-color: var(--teal);
  color: var(--dark-teal);
  cursor: pointer;
  border: none;
  border-radius: 3px;
  font-family: Josefin Sans, sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.search-btn:hover, .search-btn:focus {
  background-color: var(--darker-teal-btn);
  color: #fff;
}

.wrapper {
  padding-bottom: 40px;
}

.main-container {
  flex-flow: wrap;
  justify-content: center;
  gap: 30px 15px;
  padding: 40px 0 50px;
  display: flex;
}

.anime-card {
  color: #fff;
  background-color: var(--dark-color);
  width: 160px;
  cursor: pointer;
  border-radius: 3px;
  flex-direction: column;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 15px;
  display: flex;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.anime-image-small {
  height: 190px;
  width: 150px;
  margin-bottom: 15px;
}

.anime-small-heading {
  text-align: center;
  font-size: 11px;
}

.logo-icon:hover, .logo-icon:focus {
  transform: scale(1.05);
}

.add-button {
  color: var(--cards-color);
  background-color: var(--dark-teal);
  height: 20px;
  width: 20px;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
  font-size: 17px;
  display: flex;
}

.add-button:hover, .add-button:focus {
  background-color: var(--darker-teal-btn);
}

