.my-list-div {
  justify-content: flex-end;
  margin-bottom: 15px;
  padding-right: 15px;
  display: flex;
}

.list-link {
  color: var(--darker-teal);
  cursor: pointer;
  text-transform: uppercase;
  background: none;
  border-radius: 10px;
  padding: 7px;
  font-size: 14px;
  transition-property: color;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.25, .8, .25, 1);
  position: relative;
}

.list-link:focus, .list-link:hover {
  color: var(--teal);
}

.list-link:focus:after, .list-link:hover:after {
  width: 100%;
  left: 0%;
}

.list-link:after {
  content: "";
  pointer-events: none;
  width: 0%;
  height: 2px;
  background-color: var(--teal);
  transition-property: width, left;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.25, .8, .25, 1);
  position: absolute;
  bottom: -2px;
  left: 50%;
}

